// Import Bootstrap
import { Dropdown, DropdownButton, ButtonGroup, Offcanvas, Image, InputGroup, FormControl, Form, Row, Col, Carousel, Nav, Button, Alert, Breadcrumb, Card, CardImg, Container, Navbar, NavDropdown } from 'react-bootstrap'

// Import Link
import { Link } from 'react-router-dom'


const MenuEng = () => {
  return (
    <div>
      <Navbar id="home" bg="light" fixed="bottom" className='p-4' expand="lg" variant='light'
        style={{ zIndex: '1' }}
      >
        <Container>
          <Navbar.Brand href="https://sheekweas.github.io/scamplee/">scamplee.</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#dom">Home</Nav.Link>
              <Nav.Link href="#games">Games</Nav.Link>
              <Nav.Link href="#features">Features</Nav.Link>
              <Nav.Link href="#contact">Contact</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}

export default MenuEng